import { Header, Button, Segment, Modal, Input, Container } from 'semantic-ui-react'
//import React from 'react';
import firebase from "../utils/firebase";
import React from 'react';




function MySettings( { user } ){
    const [isNameModalOpen, setIsNameModalOpen] = React.useState(false);
    const [isPasswordModalOpen, setIsPasswordModalOpen] = React.useState(false);
    const [displayName, setDisplayName] = React.useState('');
    const [isLoading, setIsLoading] = React.useState(false);
    const [oldPassword, setOldPassword] = React.useState('');
    const [newPassword, setNewPassword] = React.useState('');

    function onSubmitName(){
        setIsLoading(true)
        user.updateProfile({
            displayName,
        }).then(() =>{
            setDisplayName('');
            setIsNameModalOpen(false);
            setIsLoading(false);
        });
    }

    function onSubmitPassword(){
        setIsLoading(true);
        const credential = firebase.auth.EmailAuthProvider.credential(
            user.email, oldPassword
        );
        user.reauthenticateWithCredential(credential).then(() => {
            user.updatePassword(newPassword).then(() => {
                setIsPasswordModalOpen(false);
                setOldPassword('');
                setNewPassword('');
                setIsLoading(false);
            });
        })
        

    }

    return(
        <Container>
            <>
            <Header></Header>
            <Header size='small'>
                
                <Button floated='right' onClick={() => setIsNameModalOpen(true)}>
                    
                </Button>
            </Header>
            <Segment vertical>{user.displayName}</Segment>
            <Modal basic open={isNameModalOpen} size='mini'>
                <Modal.Header></Modal.Header>
                <Modal.Content>
                    <Input 
                        placeholder="" 
                        value={displayName} 
                        onChange={(e) => setDisplayName(e.target.value)}
                        fluid 
                    />
                </Modal.Content>
                <Modal.Actions>
                    <Button onClick={() => setIsNameModalOpen(false)}></Button>
                    <Button onClick={onSubmitName} loading={isLoading} ></Button>
                </Modal.Actions>
            </Modal>

            </>
            <>
                <Header size='small'>
                    
                    <Button floated='right' onClick={() =>  setIsPasswordModalOpen(true)}>
                        
                    </Button>
                </Header>
                <Segment vertical>********</Segment>
                <Modal basic size='mini' open={isPasswordModalOpen}>
                    <Modal.Header></Modal.Header>
                    <Modal.Content>
                        <Modal.Header size="small"></Modal.Header>
                        <Input 
                            placeholder="" 
                            value={oldPassword} 
                            onChange={(e) => setOldPassword(e.target.value)}
                            fluid 
                        />
                        <Header></Header>
                        <Modal.Header size="small"></Modal.Header>
                        <Input 
                            placeholder="" 
                            value={newPassword} 
                            onChange={(e) => setNewPassword(e.target.value)}
                            fluid 
                        />
                    </Modal.Content>
                    
                    <Modal.Actions>
                    <Button onClick={() => setIsPasswordModalOpen(false)}></Button>
                    <Button onClick={onSubmitPassword} loading={isLoading} ></Button>
                    </Modal.Actions>
                </Modal>
            </>
        </Container>
    )
}

export default MySettings;