//import React from "react";
import { Grid, Container, Button, Embed } from 'semantic-ui-react'
import React, { useState } from 'react';
import styles from './styles.module.css'
import RwdYoutube from '../component/RwdYoutube';
import firebase from '../utils/firebase';
import { Link } from 'react-router-dom';

function Mainpage(){
    const [user, setUser] = useState(null);
    
    React.useEffect(() =>{
        firebase.auth().onAuthStateChanged((currentUser) =>{
        setUser(currentUser);
        })
    })

    return (
        <Container style={{ width: "100%", height: "100vh" }}>
        
            <Grid centered style={{ marginTop: "40px" }}>
                <div className={styles.hero_heading} style={{ fontWeight:"bold" }}>Avatar</div>
            </Grid>
            <Grid centered style={{ marginTop: "40px" }}>
                <div className={styles.hero_heading} style={{ fontWeight:"bold" }}>Classroom</div>
            </Grid>
            <Grid centered style={{ marginTop: "60px" }}>
                <div style={{fontSize: 30}}></div>
            </Grid>
            <Grid centered style={{ marginTop: "40px" }}>
                <div style={{fontSize: 30}}> Webcam </div>
            </Grid>
            {user ? (
                <></>
            ) : (
                <Grid centered style={{ marginTop: "40px" }}>
                    <Button color='positive' size='large' as={Link} to ="/signin">/</Button>
                </Grid>
            )}
            <Grid centered style={{ marginTop: "40px" }}>
                <Grid.Column width={12}>
                    <RwdYoutube
                        src="https://www.youtube.com/embed/3_E6Fre4cSg?si=gkOslkNzKeLguoxh"//need to mute and autoplay
                        controls
                        width="100%"
                    /> 
                </Grid.Column>
            </Grid>
            <br/>
            <Grid style={{ marginTop: "120px" }} >
                
                <Grid.Column floated='left' width={10}>
                    <div className={styles.small_note}></div>
                    <div className={styles.small_note}>&nbsp;&nbsp;&nbsp;</div>
                </Grid.Column>
                <Grid.Column floated='right' width={6}>
                    <div className={styles.small_note} style={{textAlign: "right"}}>In Memory of</div>
                    <div className={styles.small_note} style={{textAlign: "right"}}></div>
                </Grid.Column>
            </Grid>
            
        </Container>
    );
};


export default Mainpage;